.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.display-4 {
  font-family: 'Roboto Mono', monospace;
  font-weight: bold;
}

html {
  height:100%;
}

body {
  margin:0;
}

#footer {
  position:fixed;
  left:0px;
  bottom:0px;
  height:65px;
  width:100%;
  background-color:#515151;
}

#header {
  position:fixed;
  left:0px;
  top:0px;
  height:65px;
  width:100%;
  background: #515151
}

.bg {
  animation:slide 3s ease-in-out infinite alternate;
  background-image: linear-gradient(-60deg, #515151 50%, #000000 50%);
  bottom:0;
  left:-50%;
  opacity:.5;
  position:fixed;
  right:-50%;
  top:0;
  z-index:-1;
}

.bg2 {
  animation-direction:alternate-reverse;
  animation-duration:4s;
}

.bg3 {
  animation-duration:5s;
}

.content {
  background-color:rgba(255,255,255,.8);
  border-radius:.25em;
  box-shadow:0 0 .25em rgba(0,0,0,.25);
  box-sizing:border-box;
  left:50%;
  padding:20px;
  position:fixed;
  text-align:center;
  top:50%;
  transform:translate(-50%, -50%);
  max-height: 75%;
  overflow-y: auto;
  overflow-x: hidden;
  max-width: 75%
}

h1 {
  font-family:monospace;
}

@keyframes slide {
  0% {
    transform:translateX(-25%);
  }
  100% {
    transform:translateX(25%);
  }
}

.symbols {
font-weight: bolder;
font-size:larger
}

.header-text {
  color:blanchedalmond
}

.dcode {
  border: 1px solid #000000;
}
